.app {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
  border: 1px dotted white;
  padding: 50px 100px;
}
.img {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  overflow: hidden;
  transition: all .2s;
}
.image {
  height: 100%;
  width: 100%;
}

.img:hover {
  cursor: pointer;
  border-radius: 5px;
  transform:scale(1.1);
}
